        </el-form-item>
        <el-form-item label="" prop="appearTime" class="formitem-class">
          <div class="radio-input">
            <div v-if="noticeData.startType===0"></div>
            <!-- <div v-if="noticeData.startType===1"></div>
                            <div v-if="noticeData.startType===1">{{noticeData.appearTime}}</div>
                            <div v-if="noticeData.startType===1"></div>
                            <div v-if="noticeData.startType===2"></div>
                            <div v-if="noticeData.startType===2">{{noticeData.appearCustomTime}}</div> -->
            <!-- <div v-if="noticeData.startType===3"></div> -->
          </div>
        </el-form-item>
        <el-form-item label="2100~700" class="formitem-class" label-width='300px'>
          {{noticeData.timeSwitch?'':''}}
        </el-form-item>
        <el-form-item label="" prop="disappearTime" class="formitem-class">
          <div class="radio-input">
            <div v-if="noticeData.endType===0"></div>
            <div v-if="noticeData.endType===1"></div>
            <div v-if="noticeData.endType===1">{{noticeData.endTime}}</div>
            <div v-if="noticeData.endType===1"></div>
            <!-- <div v-if="noticeData.endType===2"></div>
                            <div v-if="noticeData.endType===2">{{noticeData.disappearCustomTime}}</div> -->
            <!-- <div v-if="noticeData.endType===3"></div> -->
          </div>
        </el-form-item>
      </el-form>
    </div>
    <div class="result-area box-class" v-if="stepNumber===3">
      <img src="../assets/resultTip.png" style="width:430px;height:67px;margin-bottom: 20px;">
      <el-button @click="goHome()" type="primary" round></el-button>
    </div>
    <div class="box-class" v-if="isEditPage&&stepNumber===1">
      <el-form label-position="left" ref="noticeData" :model="noticeData" label-width="120px" size="small" :rules="noticeRules" v-if="noticeDialogVisible" style="width: 95%;margin-top: 20px;">
        <el-form-item label="" prop="title">
          <el-input v-model="noticeData.title" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input :autosize="{ minRows: 1, maxRows: 5}" type="textarea" v-model="noticeData.content" maxlength="250"></el-input>
        </el-form-item>
        <el-form-item label="" required>
          <div>
            <el-radio v-model="noticeData.startType" :label="0"></el-radio>
            <!-- <div v-if="isShowRadioTip" style="color:rgb(160, 160, 160);margin-left: 20px;">2100~700</div> -->
            <!-- <el-radio v-model="noticeData.startType" :label="1" class="radio-class"></el-radio>
                            <el-input v-model="noticeData.appearTime" class="input-class" size='mini' :disabled="noticeData.startType!=1"></el-input>
                            <div :class="{ 'text-choose': noticeData.startType==1 }"></div>
                            <el-radio v-model="noticeData.startType" :label="2" class="radio-class"></el-radio>
                            <el-date-picker v-model="noticeData.appearCustomTime" type="datetime" placeholder="" size='mini' class="date-class" :disabled="noticeData.startType!=2">
                            </el-date-picker> -->
            <!-- <el-radio v-model="noticeData.startType" :label="3" class="radio-class"></el-radio> -->
          </div>
        </el-form-item>
        <el-form-item label="2100~700" prop="timeSwitch" label-width='300px'>
          <div class="radio-input">
            <el-switch v-model="noticeData.timeSwitch">
            </el-switch>
            <div class="radio-class">{{noticeData.timeSwitch?'':''}}</div>
          </div>
        </el-form-item>
        <el-form-item label="" required>
          <div class="radio-input">
            <el-radio v-model="noticeData.endType" :label="0" @change="numberChange(1)"></el-radio>
            <el-radio v-model="noticeData.endType" :label="1" class="radio-class" @change="noticeRadio2Choose"></el-radio>
            <el-input v-model="noticeData.endTime" class="input-class" size='mini' :disabled="noticeData.endType!=1" style="width:70px" maxlength="5" @change="numberChange"></el-input>
            <div :class="{ 'text-choose': noticeData.endType==1 }"></div>
            <!-- <el-radio v-model="noticeData.endType" :label="2" class="radio-class"></el-radio>
                            <el-date-picker v-model="noticeData.disappearCustomTime" type="datetime" placeholder="" size='mini' class="date-class" :disabled="noticeData.endType!=2">
                            </el-date-picker> -->
            <!-- <el-radio v-model="noticeData.endType" :label="3" class="radio-class"></el-radio> -->
          </div>
          <el-alert title="1~10080" type="error" v-if="isNoticeTimeError" :closable="false"></el-alert>
        </el-form-item>
      </el-form>
      <el-button @click="deleteNotice()" type="danger" v-if="noticeDialogVisible" style="margin-bottom: 20px"></el-button>
      <el-button @click="addNoticeButton()" type="primary" round class="button-class" plain v-if="!noticeDialogVisible"><i class="el-icon-plus el-icon--right"></i></el-button>
    </div>
    <div class="box-class" v-if="isDetailpage&&xiajiaReason">
      <div class="reason-area">
        <div style="font-size: 19px;font-weight: bold;"></div>
        <div>{{xiajiaReason}}</div>
      </div>
    </div>
    <div class="box-class" v-if="isDetailpage&&jinyongReason">
      <div class="reason-area">
        <div style="font-size: 19px;font-weight: bold;"></div>
        <div>{{jinyongReason}}</div>
      </div>
    </div>
    <div class="box-class" v-if="isDetailpage&&bohuiReason">
      <div class="reason-area">
        <div style="font-size: 19px;font-weight: bold;"></div>
        <div>{{bohuiReason}}</div>
      </div>
    </div>
    <div class="buttons-area box-class" v-if="isDetailpage">
      <el-button v-if="baseData.state===6" @click="shangjia()" round class="button-class" type="success"></el-button>
      <el-button v-if="baseData.state===9" @click="xiajia()" round class="button-class" type="warning"></el-button>
      <el-button v-if="baseData.state===9||baseData.state===12" @click="jinyong()" round class="button-class" type="warning"></el-button>
      <el-button v-if="baseData.upperOperateState!==-1&&baseData.upperOperateState!==2&&(baseData.state===0||baseData.state===12||baseData.state===-1)" @click="goEditPage()" round class="button-class" type="primary"></el-button>
      <el-button v-if="baseData.state===0 " @click="handleDelete()" round class="button-class" type="danger"></el-button>
      <el-button v-if="baseData.state===9||baseData.state===12||baseData.state===-1" @click="goDataPage()" round class="button-class" type="primary" disabled></el-button>
      <el-button @click="goHome()" round class="button-class" type="primary"></el-button>
    </div>
    <div class="buttons-area box-class" v-if="isCheckpage">
      <el-button @click="previewTest()" type="primary" round class="button-class"></el-button>
      <el-button @click="passButton(true)" type="primary" round class="button2-class" :disabled="isExam"></el-button>
      <el-button @click="passButton(false)" type="primary" round class="button-class" :disabled="isExam"></el-button>
    </div>
    <div class="buttons-area box-class" v-if="isEditPage&&stepNumber!==3">
      <el-button v-if="stepNumber===1" @click="cancelEditButton()" type="primary" round class="button-class"></el-button>
      <el-button v-if="stepNumber===2" @click="lastStepButton()" type="primary" round class="button-class"></el-button>
      <el-button v-if="stepNumber===1" @click="draftButton()" type="primary" round class="button-class"></el-button>
      <el-button v-if="stepNumber===1" @click="nextStepButton()" type="primary" round class="button-class"></el-button>
      <el-button v-if="stepNumber===2" @click="saveButton()" type="primary" round class="button-class"></el-button>
    </div>
    <el-dialog title="" :visible.sync="testDialogVisible" width="600px" center>
      <div class="test-dialog">
        <span></span>
        <img :src="testImgSrc" class="test-img">
        <!-- <span>USB</span> -->
        <div class="radio-input" style="margin-top: 30px;">
          <span> </span>
          <a class='download' href='http://test-bj-ws.vivo.com.cn/Zlf73fDGXFlrC6ST/2019010315/1f29f8d9e892a32a9aa7ca04b158b054/5c8904df-25a4-499e-9ac8-d02afee86dde.zip' download="" title="" target="_blank"></a>
          <span> </span>
        </div>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="examDialogVisible" width="600px" :before-close="examDialogClose" center>
      <el-form :model="baseData" ref="examData" label-width="100px">
        <el-form-item label=" " prop="reason" :rules="applyRules">
          <el-input v-model="baseData.reason" type="textarea" :autosize="{ minRows: 2, maxRows: 3}" placeholder="" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{baseData.reason?baseData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="examDialogClose(true)"> </el-button>
        <el-button type="primary" @click="examDialogClose()"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="jinyongDialogVisible" width="600px" :before-close="jinyongDialogClose" center>
      <el-form :model="baseData" ref="jinyongData" label-width="100px">
        <el-form-item label=" " prop="reason" :rules="applyRules">
          <el-input v-model="baseData.reason" type="textarea" :autosize="{ minRows: 2, maxRows: 3}" placeholder="" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{baseData.reason?baseData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="jinyongDialogClose(true)"> </el-button>
        <el-button type="primary" @click="jinyongDialogClose()"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="xiajiaDialogVisible" width="600px" :before-close="xiajiaDialogClose" center>
      <el-form :model="baseData" ref="xiajiaData" label-width="100px">
        <el-form-item label="" prop="reason" :rules="applyRules">
          <el-input v-model="baseData.reason" :autosize="{ minRows: 2, maxRows: 3}" type="textarea" placeholder="" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{baseData.reason?baseData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="xiajiaDialogClose(true)"> </el-button>
        <el-button type="primary" @click="xiajiaDialogClose()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import global from "../service/global";

export default {
  name: "activity-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    },
    isDetailpage: {
      type: Boolean,
      default: false
    },
    baseData: {}
  },
  data() {
    var weightValidate = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (/^\d+$/.test(value)) {
        let newValue = Number(value);
        if (newValue >= 0 && newValue <= 50) {
          callback();
        } else {
          callback(new Error("0-50"));
        }
      } else {
        callback(new Error("0-50"));
      }
    };
    var specialValidate = (rule, value, callback) => {
      if (/^\s+$/.test(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      grayscaleDataTypes: [],
      excelFileName: "",
      excelFile: "",
      uploadLoading: false,
      controlType: 1,
      grayscaleType: 1,
      downloadFileName: '',
      downloadFileUrl: '',
      isMantissaList1: false,
      isMantissaList2: false,
      imeiList: [],
      imeiValue: "",
      imeiData: {
        grayscaleDataType: '',
        releaseType: ''
      },
      mantissaList1: [],
      mantissaList2: [],
      isNoticeTimeError: false,
      isTimeError1: false,
      isTimeError2: false,
      startTimeDisabled: false,
      testCardUrl: "",
      isExam: false,
      xiajiaDialogVisible: false,
      jinyongDialogVisible: false,
      isEditPage: true,
      examDialogVisible: false,
      testImgSrc: "",
      testDialogVisible: false,
      xiajiaReason: "",
      jinyongReason: "",
      bohuiReason: "",
      noticeData: {
        title: "",
        content: "",
        startType: "",
        endType: "",
        endTime: "",
        timeSwitch: false
      },
      noticeDialogVisible: false,
      cardPreview: "",
      expandPreview: "",
      holidayList: global.holidayList,
      //peopleLabels: [{ key: 1, value: "" }, { key: 2, value: "" }],
      stepNumber: 1,
      basicData: {
        peopleLabels: [],
        bindType: 2
      },
      timeData: {
        type: 0,
        startTime: "",
        endTime: "",
        holiday: ""
      },
      shopData: {
        allTree: false,
        tree: [],
        appear: "",
        disappear: "",
        treeNames: ""
      },
      airportData: {
        allTree: false,
        tree: [],
        appear: "",
        disappear: "",
        treeNames: ""
      },
      metroData: {
        allTree: false,
        tree: [],
        appear: "",
        disappear: "",
        treeNames: ""
      },
      homeData: {
        appear: "",
        disappear: ""
      },
      companyData: {
        appear: "",
        disappear: ""
      },
      restData: {
        appear: "",
        disappear: ""
      },
      weightData: {
        order: "",
        attenuationDuration: ""
      },
      peopleRules: {
        // peopleLabels: [
        //   { required: true, message: "", trigger: "change" }
        // ],
        bindType: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      shopRules: {},
      airportRules: {},
      metroRules: {},
      homeRules: {},
      companyRules: {},
      restRules: {},
      timeRules: {},
      noticeRules: {
        title: [
          { required: true, message: "", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" }
        ],
        content: [
          { required: true, message: "", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" }
        ]
      },
      weightRules: {
        order: [{ required: true, validator: weightValidate, trigger: "blur" }],
        attenuationDuration: [
          { required: true, message: '', trigger: 'change' }
        ],
      },
      applyRules: [
        { required: true, message: "", trigger: "blur" },
        { required: true, validator: specialValidate, trigger: "blur" }
      ]
    };
  },
  methods: {
    grayscaleDataTypeChange(arrary) {
      if (arrary.length > 1) {
        this.grayscaleDataTypes.shift();
      }
      if (this.grayscaleDataTypes.length > 0) {
        this.imeiData.grayscaleDataType = this.grayscaleDataTypes[0];
      } else {
        this.imeiData.grayscaleDataType = '';
      }
    },
    deleteIMEI(index) {
      this.imeiList.splice(index, 1);
    },
    saveIMEI() {
      if (this.imeiList.length > 9) {
        this.$confirm("10IMEI", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
      } else if (this.imeiList.includes(this.imeiValue)) {
        this.$confirm("IMEI", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
      } else if (/^\d{15}$/.test(this.imeiValue)) {
        this.imeiList.push(this.imeiValue);
      } else {
        this.$confirm("IMEI(15)", "", {
          confirmButtonText: "",
          center: true,
          type: "warning"
        });
      }
    },
    getForNumber(number, type) {
      let newNumber = number - 1;
      if (type === 1) {
        return newNumber + '';
      } else {
        if (newNumber < 10) {
          return '0' + newNumber;
        } else {
          return newNumber + '';
        }
      }
    },
    //
    handleExceed() {
      this.$message.warning("");
    },
    removeFile() {
      this.excelFile = "";
      this.excelFileName = "";
    },
    //
    getExcelFile(data) {
      this.excelFile = data.file;
      this.excelFileName = data.file.name;
    },
    getFormatDate(date) {
      if (date) {
        let newDate = "";
        if (typeof date === 'string') {
          newDate = new Date(Date.parse(date.replace(/-/g, "/")));
        } else {
          newDate = new Date(date);
        }
        return newDate.Format("yyyy-MM-dd hh:mm:ss");
      } else {
        return "";
      }
    },
    numberChange(value) {
      if (/^\d+$/.test(value)) {
        let number = Number(value);
        if (number >= 1 && number <= 10080) {
          this.isNoticeTimeError = false;
        } else {
          this.isNoticeTimeError = true;
        }
      } else {
        this.isNoticeTimeError = true;
      }
    },
    dateChange(isStart) {
      let startDate = Date.parse(this.timeData.startTime);
      let endDate = Date.parse(this.timeData.endTime);
      let now = Date.now();
      if (isStart && startDate < now) {
        this.isTimeError2 = true;
        this.isTimeError1 = false;
      } else if (!isStart && endDate < now) {
        this.isTimeError2 = true;
        this.isTimeError1 = false;
      } else if (startDate >= endDate) {
        this.isTimeError1 = true;
        this.isTimeError2 = false;
      } else {
        this.isTimeError1 = false;
        this.isTimeError2 = false;
      }
    },
    noticeRadio2Choose() {
      this.noticeData.endTime = 120;
    },
    timeRadioChoose() {
      this.timeData.holiday = "";
      this.timeData.startTime = "";
      this.timeData.endTime = "";
      this.isTimeError1 = false;
      this.isTimeError2 = false;
    },
    //
    holidayChange(value) {
      let holiday = this.holidayList.find(holiday => {
        return holiday.value === value;
      });
      if (holiday) {
        this.timeData.startTime = holiday.startTime;
        this.timeData.endTime = holiday.endTime;
      }
    },
    //
    handleDelete() {
      let message = "" + this.baseData.name + "";
      this.$confirm(message, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          let param = {
            id: this.baseData.id,
            versionCode: this.baseData.versionCode
          };
          this.$http
            .post("/scene/del", param)
            .then(response => {
              if (response.data && response.data.code == "0") {
                this.$message({
                  type: "success",
                  message: "!"
                });
                this.$emit("activity-event", {
                  type: "home-page"
                });
              } else {
                if (response.data.msg) {
                  this.$message.error(response.data.msg);
                } else {
                  this.$message.error("");
                }
              }
            })
            .catch(() => {
              this.$message.error("");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //
    xiajiaDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.xiajiaDialogVisible = false;
      } else {
        this.$refs["xiajiaData"].validate(valid => {
          if (valid) {
            let param = {
              id: this.baseData.id,
              versionCode: this.baseData.versionCode,
              lastState: this.baseData.state,
              state: 12,
              explain: this.baseData.reason
            };
            this.$http
              .post("/scene/modifyState", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.xiajiaReason = this.baseData.reason;
                  this.baseData.reason = "";
                  this.baseData.state = 12;
                  this.xiajiaDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("");
                  }
                }
              })
              .catch(() => {
                this.$message.error("");
              });
          } else {
            this.$message.warning("");
            return false;
          }
        });
      }
    },
    //
    xiajia() {
      this.xiajiaDialogVisible = true;
    },
    //
    shangjia() {
      let param = {
        id: this.baseData.id,
        versionCode: this.baseData.versionCode,
        lastState: this.baseData.state,
        state: 9
      };
      this.$http
        .post("/scene/modifyState", param)
        .then(response => {
          if (response.data && response.data.code == "0") {
            this.baseData.state = 9;
            this.$message({
              type: "success",
              message: ""
            });
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("");
            }
          }
        })
        .catch(() => {
          this.$message.error("");
        });
    },
    //
    passButton(type) {
      if (type) {
        let param = {
          id: this.baseData.id,
          versionCode: this.baseData.versionCode,
          lastState: this.baseData.state,
          state: 6,
          explain: this.baseData.reason
        };
        this.$http
          .post("/scene/modifyState", param)
          .then(response => {
            if (response.data && response.data.code == "0") {
              this.$message({
                type: "success",
                message: ""
              });
              this.baseData.state = 6;
              this.isExam = true;
              this.$emit("activity-event", {
                type: "home-page"
              });
            } else {
              if (response.data.msg) {
                this.$message.error(response.data.msg);
              } else {
                this.$message.error("");
              }
            }
          })
          .catch(() => {
            this.$message.error("");
          });
      } else {
        this.examDialogVisible = true;
      }
    },
    //
    jinyongDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.jinyongDialogVisible = false;
      } else {
        this.$refs["jinyongData"].validate(valid => {
          if (valid) {
            let param = {
              id: this.baseData.id,
              versionCode: this.baseData.versionCode,
              lastState: this.baseData.state,
              state: -1,
              explain: this.baseData.reason
            };
            this.$http
              .post("/scene/modifyState", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.jinyongReason = this.baseData.reason;
                  this.baseData.reason = "";
                  this.baseData.state = -1;
                  this.jinyongDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("");
                  }
                }
              })
              .catch(() => {
                this.$message.error("");
              });
          } else {
            this.$message.warning("");
            return false;
          }
        });
      }
    },
    //
    jinyong() {
      this.jinyongDialogVisible = true;
    },
    //
    previewTest() {
      let url = location.origin + "/scene/getQrCode?url=" + this.testCardUrl;
      this.testImgSrc = url;
      this.testDialogVisible = true;
    },
    //
    examDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.examDialogVisible = false;
      } else {
        this.$refs["examData"].validate(valid => {
          if (valid) {
            let param = {
              id: this.baseData.id,
              versionCode: this.baseData.versionCode,
              lastState: this.baseData.state,
              state: 0,
              explain: this.baseData.reason
            };
            this.$http
              .post("/scene/modifyState", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.baseData.state = 0;
                  this.isExam = true;
                  this.examDialogVisible = false;
                  this.$emit("activity-event", {
                    type: "home-page"
                  });
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("");
                  }
                }
              })
              .catch(() => {
                this.$message.error("");
              });
          } else {
            this.$message.warning("");
            return false;
          }
        });
      }
    },
    //
    goDataPage() {
      this.$emit("activity-event", {
        type: "data-page"
      });
    },
    //
    goEditPage() {
      let endTime = Date.parse(this.baseData.endTime);
      if (this.baseData.state !== 0 && Date.now() > endTime) {
        this.$confirm(", ", "", {
          confirmButtonText: "",
          showCancelButton: false,
          type: "warning",
          center: true
        });
      } else {
        this.stepNumber = 1;
        this.isEditPage = true;
        this.isCheckpage = false;
        this.isDetailpage = false;
      }
    },
    //
    deleteNotice() {
      this.noticeData = {
        title: "",
        content: "",
        startType: "",
        endType: "",
        endTime: "",
        timeSwitch: false
      };
      this.noticeDialogVisible = false;
    },
    //
    addNoticeButton() {
      this.noticeDialogVisible = true;
    },
    //
    goHome() {
      this.$emit("activity-event", {
        type: "home-page"
      });
    },
    isEmpty(key) {
      if (typeof key === 'undefined' || key === '' || key === null) {
        return true;
      } else {
        return false;
      }
    },
    dataCheck(canEmpty) {
      //
      if (!canEmpty || !this.isEmpty(this.weightData.order)) {
        if (!/^\d+$/.test(this.weightData.order) || Number(this.weightData.order) < 0 || Number(this.weightData.order) > 50) {
          this.$confirm("", "", {
            confirmButtonText: "",
            center: true,
            type: "warning"
          });
          return false;
        }
      }
      //
      if (this.noticeDialogVisible && (!canEmpty || !this.isEmpty(this.noticeData.title))) {
        if (/^\s+$/.test(this.noticeData.title) || this.isEmpty(this.noticeData.title)) {
          this.$confirm("", "", {
            confirmButtonText: "",
            center: true,
            type: "warning"
          });
          return false;
        }
      }
      //
      if (this.noticeDialogVisible && (!canEmpty || !this.isEmpty(this.noticeData.content))) {
        if (/^\s+$/.test(this.noticeData.content) || this.isEmpty(this.noticeData.content)) {
          this.$confirm("", "", {
            confirmButtonText: "",
            center: true,
            type: "warning"
          });
          return false;
        }
      }
      //
      if (this.noticeDialogVisible && (!canEmpty || !this.isEmpty(this.noticeData.endTime))) {
        if (this.isNoticeTimeError) {
          this.$confirm("", "", {
            confirmButtonText: "",
            center: true,
            type: "warning"
          });
          return false;
        }
      }

      if (!canEmpty) {
        //
        if (this.noticeDialogVisible && this.isEmpty(this.noticeData.startType)) {
          this.$confirm("", "", {
            confirmButtonText: "",
            center: true,
            type: "warning"
          });
          return false;
        }
        //
        if (this.noticeDialogVisible && this.isEmpty(this.noticeData.endType)) {
          this.$confirm("", "", {
            confirmButtonText: "",
            center: true,
            type: "warning"
          });
          return false;
        }
        //
        if (this.isEmpty(this.timeData.type)) {
          this.$confirm("", "", {
            confirmButtonText: "",
            center: true,
            type: "warning"
          });
          return false;
        }
        //
        if (this.isEmpty(this.basicData.bindType)) {
          this.$confirm("", "", {
            confirmButtonText: "",
            center: true,
            type: "warning"
          });
          return false;
        }
        //
        if (this.isEmpty(this.weightData.attenuationDuration)) {
          this.$confirm("", "", {
            confirmButtonText: "",
            center: true,
            type: "warning"
          });
          return false;
        }
        //
        if (this.isEmpty(this.timeData.startTime) || this.isEmpty(this.timeData.endTime) || this.isTimeError1 || this.isTimeError2) {
          this.$confirm("", "", {
            confirmButtonText: "",
            center: true,
            type: "warning"
          });
          return false;
        }
        //
        if (!this.imeiData.releaseType) {
          this.$confirm("", "", {
            confirmButtonText: "",
            center: true,
            type: "warning"
          });
          return false;
        }
        //
        if (this.imeiData.releaseType === 2) {
          let mantissaList1Ready = this.isMantissaList1 && this.mantissaList1.length > 0 ? true : false;
          let mantissaList2Ready = this.isMantissaList2 && this.mantissaList2.length > 0 ? true : false;
          let imeiListReady = this.imeiData.grayscaleDataType === 1 && this.imeiList.length > 0 ? true : false;
          let excelFileReady = this.imeiData.grayscaleDataType === 2 && this.excelFile ? true : false;
          if (!(mantissaList1Ready || mantissaList2Ready || imeiListReady || excelFileReady)) {
            this.$confirm("IMEI", "", {
              confirmButtonText: "",
              center: true,
              type: "warning"
            });
            return false;
          }
        }
      }
      return true;
    },
    //
    draftButton() {
      if (this.dataCheck(true)) {
        this.prepareEditData();
      }
    },
    //
    saveButton() {
      this.prepareEditData();
    },
    //
    async prepareEditData() {
      let currentState = 0;
      if (this.stepNumber === 2) {
        currentState = 3;
      }

      let timeData = {};
      timeData.type = this.timeData.type;
      timeData.holiday = this.timeData.holiday;
      timeData.startTime = this.getFormatDate(this.timeData.startTime);
      timeData.endTime = this.getFormatDate(this.timeData.endTime);

      let rule = {
        order: this.weightData.order,
        attenuationDuration: this.weightData.attenuationDuration,
        timeRule: timeData,
        state: currentState
      };

      let imeiData = {
        releaseType: this.imeiData.releaseType,
      };
      if (this.imeiData.releaseType === 2) {
        if (this.imeiData.grayscaleDataType === 1) {
          imeiData.imeiList = this.imeiList;
        } else if (this.imeiData.grayscaleDataType === 2 && this.excelFileName && this.excelFile) {
          this.uploadLoading = true;
          imeiData.fileName = this.excelFileName;
          imeiData.fileId = await this.uploadExcel();
        }
        if (this.isMantissaList1) {
          imeiData.mantissaList1 = this.mantissaList1;
        }
        if (this.isMantissaList2) {
          imeiData.mantissaList2 = this.mantissaList2;
        }
      }

      let param = {
        activityId: this.baseData.id,
        versionCode: this.baseData.versionCode,
        currentState: currentState,
        lastState: 0,
        rule: rule,
        imeiData: imeiData
      };

      if (this.noticeDialogVisible) {
        if (this.noticeData.endType === 0) {
          this.noticeData.endTime = 0;
        } else {
          this.noticeData.endTime = Number(this.noticeData.endTime);
        }
        param.noti = this.noticeData;
      }

      if ((param.imeiData.fileName && param.imeiData.fileId) || !param.imeiData.fileName) {
        this.saveDataXhr(param);
      }
    },
    //excel
    async uploadExcel() {
      let formData = new FormData();
      formData.append("activityId", this.baseData.id);
      formData.append("imeiFile", this.excelFile);
      return new Promise((resolve) => {
        this.$http
          .post('/scene/upoloadGreyImeiFile', formData)
          .then(response => {
            if (response.data && response.data.code == 0) {
              let fileId = response.data.data;
              resolve(fileId);
            } else {
              this.uploadLoading = false;
              if (response.data.msg) {
                this.$message.error(response.data.msg);
              } else {
                this.$message.error("");
              }
              resolve('');
            }
          })
          .catch(() => {
            this.uploadLoading = false;
            this.$message.error("");
            resolve('');
          });
      });
    },
    //xhr
    saveDataXhr(param) {
      this.$http
        .post("/scene/editRuleNNoti", param)
        .then(response => {
          this.uploadLoading = false;
          if (response.data && response.data.code == "0") {
            if (this.stepNumber === 1) {
              this.$message({
                type: "success",
                message: ""
              });
            } else {
              this.stepNumber++;
            }
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("");
            }
          }
        })
        .catch(() => {
          this.uploadLoading = false;
          this.$message.error("");
        });
    },
    //
    lastStepButton() {
      this.stepNumber--;
    },
    //
    nextStepButton() {
      if (this.dataCheck(false)) {
        this.stepNumber++;
      }
    },
    //
    timeCompute(date, time) {
      let d = new Date(date);
      let d2 = d.getTime() + time * 60000;
      let d3 = new Date(d2);
      return d3.Format("yyyy-MM-dd hh:mm:ss");
    },
    //
    cancelEditButton() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$emit("activity-event", {
            type: "home-page"
          });
        })
        .catch(() => { });
    },
    //xhr
    getDataXhr() {
      let param = {
        activityId: this.baseData.id,
        versionCode: this.baseData.versionCode
      };
      this.$http
        .get("/scene/getActivityInfo", { params: param })
        .then(response => {
          if (
            response.data &&
            response.data.code == "0" &&
            response.data.data
          ) {
            let activityDTO = response.data.data;
            this.testCardUrl = activityDTO.cardUrl;
            this.cardPreview = activityDTO.cardPreview;
            this.expandPreview = activityDTO.expandPreview;
            if (this.baseData.state === 12) {
              this.xiajiaReason = activityDTO.modifyReason;
            } else if (this.baseData.state === -1) {
              this.jinyongReason = activityDTO.modifyReason;
            } else if (this.baseData.state === 0) {
              this.bohuiReason = activityDTO.modifyReason;
            }
            if (activityDTO.noti) {
              this.noticeData = activityDTO.noti;
              this.noticeDialogVisible = true;
            }
            if (activityDTO.rule) {
              this.timeData = typeof activityDTO.rule.timeRule !== 'undefined'
                ? activityDTO.rule.timeRule
                : {};
              this.weightData.order = typeof activityDTO.rule.order !== 'undefined'
                ? activityDTO.rule.order
                : "";
              this.weightData.attenuationDuration = typeof activityDTO.rule.attenuationDuration !== 'undefined'
                ? activityDTO.rule.attenuationDuration
                : "";
              if (Date.parse(this.timeData.startTime) < Date.now()) {
                this.startTimeDisabled = true;
              }
            }
            if (activityDTO.imeiData) {
              this.imeiData.releaseType = activityDTO.imeiData.releaseType;
              if (this.imeiData.releaseType === 2) {
                if (activityDTO.imeiData.imeiList && activityDTO.imeiData.imeiList.length > 0) {
                  this.imeiList = activityDTO.imeiData.imeiList;
                  this.imeiData.grayscaleDataType = 1;
                  this.grayscaleDataTypes = [1];
                } else if (activityDTO.imeiData.fileName) {
                  this.downloadFileUrl = activityDTO.imeiData.fileId;
                  this.downloadFileName = activityDTO.imeiData.fileName;
                  this.imeiData.grayscaleDataType = 2;
                  this.grayscaleDataTypes = [2];
                }
                if (activityDTO.imeiData.mantissaList1 && activityDTO.imeiData.mantissaList1.length > 0) {
                  this.mantissaList1 = activityDTO.imeiData.mantissaList1;
                  this.isMantissaList1 = true;
                }
                if (activityDTO.imeiData.mantissaList2 && activityDTO.imeiData.mantissaList2.length > 0) {
                  this.mantissaList2 = activityDTO.imeiData.mantissaList2;
                  this.isMantissaList2 = true;
                }
              }
            }
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //
    getDisappearTime(date, afterDate) {
      let d1 = new Date(date);
      let d2 = new Date(afterDate);
      let t = d2.getTime() - d1.getTime();
      if (t > 0) {
        return Math.round(t / 60000);
      }
    }
  },
  //
  mounted: function () {
    if (this.isCheckpage || this.isDetailpage) {
      this.isEditPage = false;
      this.stepNumber = 2;
    }
    if (typeof this.baseData.id !== 'undefined') {
      this.getDataXhr();
    }
  },
  created: function () {
    //this.getDataXhr();
  }
};
</script>

